import React from 'react'

const RegisterForm = ({
    handleRegister,
    name,setName,
    username,setUsername,
    password,setPassword
}) => {
    return(
        <form onSubmit={handleRegister}>
         name
          <input type='text' name='name' value={name} onChange={({target})=> setName(target.value)} /><br />
            username
            <input name='username' value={username} onChange={({target}) => setUsername(target.value)} /><br />
          password
          <input type='password' name='password' value={password} onChange={({target}) => setPassword(target.value)} /><br />
              <button type='submit' > Register</button>          
        </form>
    )
}



export default RegisterForm